import React, { Component } from 'react';

import Wrapper from '../../hoc/Wrapper/Wrapper';
import classes from './ControlPanel.css';

class ControlPanel extends Component {
    rowInput = '';
    columnInput = '';
    lightsStateInput = '';

    getInputData = () => {
        return {
            rowNum: this.rowInput.value,
            columnNum: this.columnInput.value,
            lightsState: this.lightsStateInput.value
        }
    }

    render() {
        // const inputData = {
        //     rowNum: this.rowInput.value,
        //     columnNum: this.columnInput.value,
        //     lightsState: this.lightsStateInput.value
        // };
        let subPanel0 = null,
            subPanel3 = null;
        if (this.props.mode === 0) {
            subPanel0 = (
                <Wrapper>
                    <div className={classes.seperator}></div>
                    <div className={classes.inputElement}>
                        <span className={classes.label}>2~9</span>
                        <input className={classes.input} type="text" ref={(inp) => { this.rowInput = inp }} />
                    </div>
                    <div className={classes.inputElement}>
                        <span className={classes.label}>2~9</span>
                        <input className={classes.input} type="text" ref={(inp) => { this.columnInput = inp }} />
                    </div>
                    <div className={classes.inputElement}>
                        <span className={classes.label}></span>
                        <textarea className={classes.input} rows="10" ref={(inp) => { this.lightsStateInput = inp }}></textarea>
                        <p className={classes.hint}>341001 0100 100110</p>
                    </div>
                    <div className={classes.inputElement}>
                        <input
                            type="button"
                            value=""
                            className={classes.submit}
                            onClick={() => { this.props.submit(this.getInputData()) }} />
                    </div>
                </Wrapper>
            )
        }

        if (this.props.mode === 3) {
            subPanel3 = (
                <Wrapper>
                    <div className={classes.seperator}></div>
                    <p className={classes.title}><strong></strong></p>
                    <p className={classes.paragraph}>()345</p>
                    <p className={classes.title}><strong></strong></p>
                    <p className={classes.paragraph}></p>
                </Wrapper>

            )
        }
        return (
            <Wrapper>
                <div
                    className={classes.section}
                    onClick={this.props.generatePuzzle}
                    style={{ color: this.props.mode === 3 ? "rgb(51, 103, 214)" : "rgb(90, 90, 90)" }}>
                </div>
                <div
                    className={classes.section}
                    onClick={this.props.puzzleMyself}
                    style={{ color: this.props.mode === 0 ? "rgb(51, 103, 214)" : "rgb(90, 90, 90)" }}>
                    </div>
                <div
                    className={classes.section}
                    onClick={this.props.haveATry}
                    style={{ color: this.props.mode === 1 ? "rgb(51, 103, 214)" : "rgb(90, 90, 90)" }}>
                </div>
                <div
                    className={classes.section}
                    onClick={this.props.getAnswer}
                    style={{ color: this.props.mode === 2 ? "rgb(51, 103, 214)" : "rgb(90, 90, 90)" }}>
                </div>
                {subPanel0}
                {subPanel3}
            </Wrapper>

        )
    }

};

export default ControlPanel;


